import axios from 'axios';

const state = {
  user: null,
};

const getters = {
  isAuthenticated: state => !!state.user,
  stateUser: state => state.user,
};

const actions = {
  async register({dispatch}, form) {
    let UserForm = new FormData();
    UserForm.append('username', form.username);
    UserForm.append('password', form.password);
    await axios.post('user', UserForm);
    await dispatch('logIn', UserForm);
  },
  async logIn({dispatch}, user) {
    try {
        const response = await axios.post('token',
            {"username": user.username, "password": user.password},
            {withCredentials: true} // Necessary for cookies to be included in cross-site requests
        );
        if (!response.ok) {
            throw new Error(`Error: ${response.status}`);
        }
        const result = await response.json();
        const token = result.id_token;
        const expiresIn = result.expires_in;
        document.cookie = `Authorization=Bearer ${token}; path=/; Secure; HttpOnly; SameSite=None; max-age=${expiresIn}`;
        await dispatch('viewMe');
    } catch (error) {
        console.error('Login failed:', error);
    }
  },
  async viewMe({commit}) {
    let {data} = await axios.get('user');
    await commit('setUser', data);
  },
  async logOut({commit}) {
    let user = null;
    commit('logout', user);
  }
};

const mutations = {
  setUser(state, username) {
    state.user = username;
  },
  logout(state, user){
    state.user = user;
  },
};

export default {
  state,
  getters,
  actions,
  mutations
};
